@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Azeret+Mono:wght@200&display=swap');

/* Colors */
$white: #fff;
$global-color: #118AB2;
$global-text-color: #333;
$global-session-color: #2A9D8F;
$global-break-color: #E76F51;
$global-error-color: #F3950D;

/* font-family */
$global-courier-font: 'Azeret Mono', monospace;

/* generic font-sizes */
$size-x3: 3rem; // 48px
$size-x2: 2rem; // 32px
$size-x15: 1.5rem; // 24px
$size-x1: 1rem; // 16px

/* breakpoints */
$bp-small: 680px;
$bp-medium: 960px;
$bp-large: 1200px;
$bp-xlarge: 1600px;